.wrapper {
	margin: 0 2.4rem;
}

@media screen and (min-width: 1200px) {
	.wrapper {
		margin: 0 8rem;
	}
}

@media screen and (min-width: 1440px) {
	.wrapper {
		max-width: 1440px;
		margin: 0 12rem;
	}
}

@media screen and (min-width: 1800px) {
	.wrapper {
		margin: 0 auto;
	}
}
